body{
  background-color:#101010 ;
}

.Thread{
  padding: 10px;
  margin: 20px;
  /* border-bottom: 2px solid gray; */
  width: 90vw;
}

.Thread:last-child{
  border-bottom: none;
}

.threadContainer{
  padding: 10px;
  color: white;
  padding: 10px;
  display: grid;
  grid-template-rows: auto 1fr;
  grid-gap: 10px;
  text-align: left;
  font-size: 1.05em;
  align-self: stretch;
  background-color: grey;
  opacity: 0.9;
  border-radius: 8px;
  display: flex;
  flex-direction: column;
}


.image{
  float: left;
  margin-right: 10px;
}

.threadInfo{
  /* display: inline-flex; */
  gap: 10px;
}

.threadInfoContainer{
  display: inline-flex;
  gap: 10px;
  /* padding-bottom: 10px; */
}

.postNo{
  cursor: pointer;
}

#user{
 color: #00ff46;
}
